import { Controller, Commands } from '@ali/4ever-cangjie';
import { LinkNode } from '../../actions';
import { removeLink } from '../../commands';
import { ILinkCardInfo, LinkPluginConfig } from '../../types';
import buildLinkCardBlock from '../utils/buildLinkCardBlock';
import { fetchLinkData } from '../utils/fetchLinkData';
import deleteExtraBlankLine from './deleteExtraBlankLine';


export default function convertTextLinkToBlockLinkCard(
  controller: Controller,
  node: LinkNode,
  data: {
    text: string,
    href: string,
    cardInfo?: ILinkCardInfo,
    getLinkInfo?: LinkPluginConfig['getLinkInfo']
  }
) {

  const { text, href, cardInfo, getLinkInfo } = data;

  // 将 text link 转换为 block link card
  const nodeText = text.length ? text : href;

  // 如果已经有 cardInfo，则不需要拉取链接信息
  const needFetchLinkInfo = !Boolean(cardInfo);

  // 如果更新了卡片信息
  const card = buildLinkCardBlock({ text: nodeText, href, cardInfo: cardInfo || {} });
  controller
    .setOpRelatedId(card.key)
    .command(Commands.moveToStartOfNode, node)
    .command(removeLink, node)
    .command('insertCard', card)
    .command(deleteExtraBlankLine, card.key);

  if (needFetchLinkInfo) {
    fetchLinkData(card, controller, href, nodeText, getLinkInfo, card.key);
  }
  return controller;
};

